'use client'

import { useState } from "react";
import { Checkbox } from "@/components/ui/checkbox";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Switch } from "@/components/ui/switch"; 
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input"; // <--- NEW
import { Pencil, Check, X } from "lucide-react"; // <--- NEW ICONS

type Props = {
  roles: any[];
  permissions: any[];
  existingAssignments: any[];
  onToggle: (roleId: string, permId: string, isChecked: boolean) => void; 
  onRoleStatusChange: (roleId: string, status: boolean) => void;
  onRenameRole: (roleId: string, newName: string) => void; // <--- NEW PROP
};

export default function PermissionMatrix({ 
  roles, 
  permissions, 
  existingAssignments, 
  onToggle, 
  onRoleStatusChange,
  onRenameRole
}: Props) {
  
  // Track which role is currently being edited
  const [editingRoleId, setEditingRoleId] = useState<string | null>(null);
  const [tempName, setTempName] = useState("");

  const startEditing = (role: any) => {
    setEditingRoleId(role.id);
    setTempName(role.name);
  };

  const saveName = () => {
    if (editingRoleId && tempName.trim()) {
      onRenameRole(editingRoleId, tempName);
    }
    setEditingRoleId(null);
  };

  const cancelEditing = () => {
    setEditingRoleId(null);
  };

  const isAssigned = (roleId: string, permId: string) => {
    return existingAssignments.some(a => a.role_id === roleId && a.permission_id === permId);
  };

  return (
    <div className="border rounded-lg overflow-hidden">
      <Table>
        <TableHeader>
          <TableRow className="bg-slate-100">
            <TableHead className="w-[200px] font-bold">Permission</TableHead>
            {roles.map(role => (
              <TableHead key={role.id} className="text-center min-w-[160px]">
                <div className="flex flex-col items-center gap-2 py-2">
                  
                  {/* EDITABLE NAME SECTION */}
                  {editingRoleId === role.id ? (
                    <div className="flex items-center gap-1 animate-in fade-in zoom-in duration-200">
                      <Input 
                        value={tempName} 
                        onChange={(e) => setTempName(e.target.value)} 
                        className="h-7 text-xs w-24 px-1 bg-white"
                        autoFocus
                        onKeyDown={(e) => {
                          if (e.key === 'Enter') saveName();
                          if (e.key === 'Escape') cancelEditing();
                        }}
                      />
                      <button onClick={saveName} className="text-green-600 hover:text-green-700"><Check className="w-4 h-4" /></button>
                      <button onClick={cancelEditing} className="text-red-500 hover:text-red-600"><X className="w-4 h-4" /></button>
                    </div>
                  ) : (
                    <div 
                      className="flex items-center gap-2 group cursor-pointer hover:bg-slate-200/50 px-2 py-1 rounded transition-colors"
                      onClick={() => startEditing(role)}
                    >
                      <span className={`font-bold ${role.is_enabled ? 'text-black' : 'text-slate-400'}`}>
                        {role.name}
                      </span>
                      <Pencil className="w-3 h-3 text-slate-400 opacity-0 group-hover:opacity-100" />
                    </div>
                  )}
                  
                  {/* ENABLE/DISABLE TOGGLE */}
                  <div className="flex items-center gap-2">
                    <Switch 
                      checked={role.is_enabled} 
                      onCheckedChange={(checked) => onRoleStatusChange(role.id, checked)}
                      className="scale-75" 
                    />
                    {!role.is_enabled && <Badge variant="destructive" className="text-[10px] px-1 h-4">Disabled</Badge>}
                  </div>
                </div>
              </TableHead>
            ))}
          </TableRow>
        </TableHeader>
        <TableBody>
          {permissions.map(perm => (
            <TableRow key={perm.id}>
              <TableCell className="font-medium text-slate-600">
                {perm.slug}
              </TableCell>
              {roles.map(role => (
                <TableCell key={role.id} className="text-center p-0">
                  <div className="flex justify-center py-4">
                    <Checkbox 
                      checked={isAssigned(role.id, perm.id)}
                      onCheckedChange={(checked) => onToggle(role.id, perm.id, checked as boolean)}
                      disabled={!role.is_enabled} 
                    />
                  </div>
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}